import React from 'react';
import {Grid, Typography, BottomNavigation, BottomNavigationAction, Box, Paper} from '@material-ui/core';

import { Link } from 'react-router-dom';
import { Instagram, Twitter, Policy, Store } from '@material-ui/icons';
import useStyles from './FooterStyles';
import { useNavigate } from 'react-router-dom';

const Footer = () => {
    let classes=useStyles();
    let navigate=useNavigate();
  


  return (
      <>

      <main> 
    
      <div className={classes.toolbar} align='center'>
      <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
    <Box align='center'>
      <BottomNavigation
        showLabels
        align='center'
      >
      
        
        <BottomNavigationAction 
        label={<Typography className={classes.text} >
          Instagram
        </Typography>} 
        icon={<Instagram />} 
        href="https://www.instagram.com/bookherobiz/"
        target="_blank"
        />

        <BottomNavigationAction 
        label={<Typography 
        className={classes.text} >Twitter</Typography>} 
        icon={<Twitter/>} 
        
        />  

        <BottomNavigationAction label={<Typography className={classes.text} >Policies</Typography>} icon={<Policy/>}/>
        <BottomNavigationAction label="Book Hero, LLC" className={classes.text} icon={<Store/>}/>
        
      </BottomNavigation>
     
    </Box>
     </Paper>
    </div>
  
    
    </main>
    </>
  );
  
  
  
}

export default Footer;

   