import React from "react";
import { Grid} from "@material-ui/core";

import ProductReviewCard from './Product/ProductUpdate';
import useStyles from "./productsStyles";

// import logo from "../../assets/BookHero.png";

import ProductNavBar from "../ProductNavBar/ProductNavBar";

const Products = ({ products, onAddToCart }) => {
  const classes = useStyles();
  return (
    <React.Fragment>
      
     
      <main className={classes.content}>
       
        <div className={classes.toolbar} />
        <Grid container justifyContent="center" spacing={5}>
          <Grid item xs={12} sm={12} md={3} lg={3}>
            
            <ProductNavBar product={products} onAddToCart={onAddToCart}></ProductNavBar>
            
          </Grid>
          {products.map((product) => (
            <Grid item key={product.id} xs={12} sm={6} md={4} lg={3}>
              <ProductReviewCard product={product} onAddToCart={onAddToCart} />
            </Grid>
          ))}
        </Grid>
      </main>
    
    </React.Fragment>
  );
};

export default Products;
