import React from 'react';
import {Card, Typography, CardHeader, IconButton, CardMedia, CardContent, CardActions, Collapse} from '@material-ui/core';
import { AddShoppingCartOutlined, Favorite, Share, ExpandMore } from '@material-ui/icons';
import ExpandMoreContent from './ExpandMoreContent';


import useStyles from './styles';


  const ProductReviewCard=({ product, onAddToCart })=> {
      const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);
  
    const handleExpandClick = () => {
      setExpanded(!expanded);
    };
  
    return (
      <Card className={classes.background}>
        <CardHeader
          className={classes.text}
          align='center'
          title= {product.name}
          
          
        />
        <CardMedia
           className={classes.media}
           image={product.image.url}
           title={product.name}
           align='center'
        />
        <CardContent>
          <Typography variant="h5" className={classes.text}>
          {product.price.formatted_with_symbol}
          </Typography>
        </CardContent>
        <CardActions disableSpacing>
          <Typography variant='body2' className={classes.text}>
            Expand for Product Details
          </Typography>
          <ExpandMoreContent
            expand={expanded}
            onClick={handleExpandClick}
            aria-expanded={expanded}
            aria-label="show more"
            className={classes.text}
          >
            <ExpandMore />
          </ExpandMoreContent>
        </CardActions>
        <Collapse in={expanded} timeout="auto" unmountOnExit>
          <CardContent>
          <Typography
          dangerouslySetInnerHTML={{ __html: product.description }}
          variant="body2"
          className={classes.text}
        />
             <CardActions disableSpacing className={classes.cardActions}>
        <IconButton
          aria-label="Add to Cart"
          onClick={() => onAddToCart(product.id, 1)}
        >
          <AddShoppingCartOutlined style={{color: 'white'}}/>
        </IconButton>
      </CardActions>
          </CardContent>
        </Collapse>
      </Card>
    );
  }

  export default ProductReviewCard;