import * as React from 'react';

import {Box, List, ListItem, Button, ListItemIcon, ListItemText, Divider} from '@material-ui/core';
import {FilterVintage, Star} from '@material-ui/icons';

import { useNavigate } from 'react-router-dom';


import useStyles from './ProductNavBarStyles';





export default function BasicList( product, onAddToCart) {
let classes=useStyles();
let navigate = useNavigate();



const onAllProductsClicked =()=>{
  let path = `/products`;
  navigate(path);
}



  return (
    <Box sx={{ width: '100%', maxWidth: 360, bgcolor: '#B42026' }}>
      <nav aria-label="main mailbox folders">
        <List>
          <ListItem >
            <Button onClick={onAllProductsClicked}>
              <ListItemIcon>
                <FilterVintage className={classes.icon}/>
              </ListItemIcon>
              <ListItemText className={classes.text} primary="All Products" />
            </Button>
          </ListItem>
          <ListItem >
            <Button>
              <ListItemIcon>
                <Star className={classes.icon}/>
              </ListItemIcon>
              <ListItemText className={classes.text} primary="Featured" />
            </Button>
          </ListItem>
        </List>
      </nav>
      <Divider />
      <nav aria-label="secondary mailbox folders">
        <List>
          <ListItem >
          
            <Button component="a" href="/printables">
        <ListItemText primary="Printables" className={classes.text}/>
      </Button>
        
        
        
       
        
          </ListItem>
          
          <ListItem >
            <Button component="a" href="/holidayactivitybooks">
              <ListItemText className={classes.text} primary="Holiday Bundles" />
            </Button>
          </ListItem>
          <ListItem>
            <Button component="a" href="/guidedjournals">
              <ListItemText className={classes.text} primary="Be Your Own Boss Packages" />
            </Button>
          </ListItem>
        </List>
      </nav>
    </Box>
  );
}
