import React from "react";
import {
  Typography,
 Grid,
 ListItem,
 IconButton,
List,
ListItemAvatar,
ListItemText,
Avatar

} from "@material-ui/core";

import useStyles from "./CartItemStyles";


// function generate(element) {
//     return [0, 1, 2].map((value) =>
//       React.cloneElement(element, {
//         key: value,
//       }),
//     );
//   }
  

export const CartItemList = ({ item, onUpdateCartQty, onRemoveFromCart }) => {
    const classes = useStyles();

return(
    <>
    



  <List>
    
      <ListItem>
        <Grid container spacing={2}>
      <Grid item xs={6} sm={6} md={3}  lg={3}>
        <ListItemAvatar>
          <Avatar
          src={item.image.url}
           alt={item.name} 
           className={classes.media}
    />
        </ListItemAvatar>
        </Grid>
        < Grid item xs={6} sm={6} md={3}  lg={3}>
        <ListItemText>
        <Typography className={classes.text}>{item.name}</Typography>
        
        </ListItemText>
       </Grid>
        <Grid item xs={6} sm={6} md={3} lg={3}>
        <div className={classes.buttons}>
          <IconButton
            type="button"
          
            onClick={() => onUpdateCartQty(item.id, item.quantity - 1)}
          >
            -
          </IconButton>
          <Typography >{item.quantity}</Typography>
          <IconButton
            type="button"
          
            onClick={() => onUpdateCartQty(item.id, item.quantity + 1)}
          >
            +
          </IconButton>
        </div>
        </Grid>
        <Grid item xs={6} sm={6} md={3}  lg={3}>
        <IconButton  onClick={() => onRemoveFromCart(item.id)}>
               <Typography>Remove</Typography> 
          </IconButton>
          </Grid>
          </Grid>
          <div>
          <Grid container>
          <Grid item xs={12} sm={12} md={12}  lg={12}>
          <ListItemText align='right'>
          <Typography className={classes.text} style={{fontWeight: 'bold'}}>
         {item.line_total.formatted_with_symbol}
        </Typography>
          </ListItemText>
          </Grid>
           </Grid>  
           </div>
      </ListItem>
    
  </List>

 </>

);
};

export default CartItemList;