import React, {useState} from 'react';
import useStyles from './contactStyles';
import { useForm, FormProvider } from 'react-hook-form';
import {Grid, Typography, Button, TextField, Card, CardContent, CardMedia } from '@material-ui/core';
import logo from '../../assets/BookHero.png';
import { init, sendForm } from 'emailjs-com';
init('Yoyw5S9iZ4qoSwyh2');


const ContactUs = () => {
let classes=useStyles();

const [contactNumber, setContactNumber] = useState("000000");


const { register, handleSubmit, watch, errors } = useForm();
const message = watch('message') || "";
const messageCharsLeft = 1500 - message.length;

const generateContactNumber = () => {
      const numStr = "000000" + (Math.random() * 1000000 | 0);
      setContactNumber(numStr.substring(numStr.length - 6));
    }

const onSubmit = () => {
  
  const form = document.querySelector('#contact-form');
  generateContactNumber();
  sendForm('contact_form', 'template_k4ouk7k', '#contact-form')
    .then(function(response) {
      console.log('SUCCESS!', response.status, response.text);
      form.reset();
     
    }, function(error) {
      console.log('FAILED...', error);
      
    });
  };



  return (
    <>
    <main className={classes.background}>
    <div className={classes.toolbar} />
    <Grid container className={classes.layout}spacing={5}>
    <Grid item xs={12} sm={12} md={12} lg={12} >
      <div className={classes.border}>
    <Card align='center'>
    <CardMedia component='img' src={logo} alt="logo" className={classes.logo}/>
      <CardContent>
    <Typography variant='h3' style={{fontFamily: 'Roboto'}}>Contact Us</Typography> 
  </CardContent>
    
  <CardContent>
    <div align='center'>
      <FormProvider> 
      <form id='contact-form' align='center' onSubmit={handleSubmit(onSubmit)}>
      <input type='hidden' name='contact_number' value={contactNumber} />
     
  <TextField 
  type='text' 
  name='username' 
  label='Name' 
  className={classes.input} 
  fullWidth
  {...register('username', { required: true })}/>
  <br/>

  <TextField type='email'
   name='user_email' 
  label='Email' 
  className={classes.input} 
  fullWidth
  {...register('user_email', { required: true })}/>
  <br/>

  <TextField
  name='message' 
  label='Message' 
  className={classes.textarea} 
  {...register('message', { required: true })}
  maxLength='1500'
  fullWidth
  multiline
  rows={6}
  />
  <p className={classes.wordCount} >Characters Left: {messageCharsLeft}</p> 
  <br/>
  <Button type='submit' variant='contained' fullWidth className={classes.button} >Send</Button>
</form>

</FormProvider>
    </div>
    </CardContent>
    </Card>
    </div>
    </Grid>
    </Grid>

    </main>
    
    </>
  );
}

export default ContactUs;