import { makeStyles } from '@material-ui/core/styles';

export default makeStyles((theme) => ({
  toolbar: theme.mixins.toolbar,
  title: {
    marginTop: '2%',
    color: 'white',
    fontFamily: 'Roboto',
    align: 'center'
  },
  emptyButton: {
    minWidth: '150px',
    [theme.breakpoints.down('xs')]: {
      marginBottom: '5px',
    },
    [theme.breakpoints.up('xs')]: {
      marginRight: '20px',
    },
  },
  checkoutButton: {
    minWidth: '150px',
  },
  link: {
    textDecoration: 'none',
  },
  cardDetails: {
    display: 'flex',
    marginTop: '10%',
    width: '100%',
    justifyContent: 'space-between',
    backgroundColor: theme.palette.grey[800],
    paddingTop: '10px',
    paddingBottom: '20px'

  },

  text: {
    color: 'white',
    fontFamily: 'Roboto'
  },

  background: {
    backgroundColor: theme.palette.grey[800]
  },

  list: {
    backgroundColor: 'white',
    [theme.breakpoints.up(600 + theme.spacing(3))]: {
      width: 600,
      marginLeft: 'auto',
      marginRight: 'auto',
    },

  }
}));