import * as React from 'react';
import {Tab, Tabs, Typography, Box} from '@material-ui/core';

import { Link } from 'react-router-dom';

import Products from './Products';
import Printables from './Printables';
import HolidayBooks from './HolidayBooks';
import GuidedJournals from './GuidedJournals';



function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`vertical-tabpanel-${index}`}
        aria-labelledby={`vertical-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box sx={{ p: 3 }}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }
  
 
  function a11yProps(index) {
    return {
      id: `vertical-tab-${index}`,
      'aria-controls': `vertical-tabpanel-${index}`,
    };
  }
  
   const ProductTabs=()=> {
    const [value, setValue] = React.useState(0);
  
    const handleChange = (event, newValue) => {
      setValue(newValue);
    };
  
    return (
        <>
      <Box
        sx={{ flexGrow: 1, bgcolor: 'background.paper', display: 'flex', height: 224 }}
      >
        <Tabs
          orientation="vertical"
          variant="scrollable"
          value={value}
          onChange={handleChange}
          aria-label="Vertical tabs example"
          sx={{ borderRight: 1, borderColor: 'divider' }}
        >
          <Tab label="All Products" {...a11yProps(0)} />
          <Tab label="Featured" {...a11yProps(1)} />
          <Tab label="Printables" {...a11yProps(2)} />
          <Tab label="Holiday Bundles" {...a11yProps(3)} />
          <Tab label="Custom Orders" {...a11yProps(4)} />
        </Tabs>
        <TabPanel value={value} index={0}>
         <Link>All Products</Link>
        </TabPanel>
        <TabPanel value={value} index={1}>
         
        </TabPanel>
        <TabPanel value={value} index={2}>
         
        </TabPanel>
        <TabPanel value={value} index={3}>
          
        </TabPanel>
        <TabPanel value={value} index={4}>
          
        </TabPanel>
        
      </Box>
      </>
    );
  }

  export default ProductTabs; 