import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({

    input: {

    },
    textarea: {
        height: '10em',

    },

    wordCount: {

        margin: 'auto',
        talign: 'left'
    },

    statusMessage: {
        opacity: 0
    },
    success: {
        opacity: 1,
        color: 'green'
    },

    failure: {
        opacity: 1,
        color: 'red'
    },

    background: {
        backgroundColor: theme.palette.grey[800]
    },

    layout: {

        width: 'auto',
        marginLeft: theme.spacing(3),
        marginRight: theme.spacing(3),

        [theme.breakpoints.up(1100 + theme.spacing(3))]: {
            width: 1100,
            marginLeft: 'auto',
            marginRight: 'auto',
        },

    },

    button: {
        backgroundColor: '#B42026',
        color: 'white',
        fontFamily: 'Roboto',

    },

    border: {
        border: 'solid 10px',
        borderColor: '#B42026'
    },

    logo: {
        height: '20%',
        width: '20%',
        justifyContent: 'center',
        objectFit: 'cover'

    },

    toolbar: theme.mixins.toolbar,
}))