import React from "react";
import {
  AppBar,
  Toolbar,
  IconButton,
  Badge,
  Box,
  Typography,
  Grid
} from "@material-ui/core";
import { ShoppingCart } from "@material-ui/icons";
import { Link } from "react-router-dom";


import useStyles from "./navBarStyles";

import BasicMenu from "./menu";

const Navbar = ({ totalItems }) => {
  const classes = useStyles();
  

  return (
    <>
    <Box>
      <AppBar position="sticky" align='center' color="inherit" className={classes.appBar}>
        <Toolbar>
          <Grid container>
            <Grid item xs={6} sm={6} md={3} lg={3}>
            <Typography
            component={Link}
            to="/"
            variant="h6"
            className={classes.title}
            color="inherit"
          >
            HOME
          </Typography>
          </Grid>
        
          <div className={classes.title} >
            <Grid item xs={6} sm={6} md={3} lg={3}>
                <BasicMenu />
          </Grid>
            </div>
        
        <Grid item xs={6} sm={6} md={3} lg={3}>
          <Typography
            component={Link}
            to="/aboutus"
            variant="h6"
            className={classes.title}
            color="inherit"
          
          >
            ABOUT
          </Typography>
          </Grid>
          <Grid item xs={6} sm={6} md={3} lg={3}>
          <Typography
            component={Link}
            to="/contactus"
            variant="h6"
            className={classes.title}
            color="inherit"
          >
            CONTACT
          </Typography>
          </Grid>
            </Grid>
          
          
         
        
          <div className={classes.grow} />
          {/* {location.pathname === !"/cart"  && ( */}
            <div >
              <IconButton
                component={Link}
                to="/cart"
                aria-label="Show Cart Items"
                color="inherit"
              >
                <Badge badgeContent={totalItems} color="secondary">
                  <ShoppingCart />
                </Badge>
              </IconButton>
            </div>
          {/* )} */}
         
        </Toolbar>
      </AppBar>
      </Box>
      
      
    </>
  );
};

export default Navbar;
