import { makeStyles } from "@material-ui/core/styles";

export default makeStyles((theme) => ({


    root: {
        maxWidth: '100%'
    },
    media: {
        height: '100%',
        width: '100%',
        paddingTop: '100%',
        justifyContent: 'center',

    },
    cardActions: {
        display: 'flex',
        justifyContent: 'flex-end',
    },
    cardContent: {
        display: 'flex',
        justifyContent: 'space-between',
    },

    background: {
        backgroundColor: theme.palette.grey[800],
        border: 'solid 10px',
        borderColor: '#B42026'

    },

    text: {
        color: 'white',
        fontWeight: 'bold',
        fontFamily: 'Roboto'
    }


}));