import { makeStyles } from '@material-ui/core/styles';

export default makeStyles(() => ({
  media: {
    height: 100,
    width: 100
  },
  cardContent: {
    display: 'flex',
    justifyContent: 'space-between',
  },
  cartActions: {
    justifyContent: 'space-between',
  },
  buttons: {
    display: 'flex',
    alignItems: 'center',
  },

  text: {
    fontFamily: 'Roboto'
    , paddingLeft: '10px'
  },

  border: {
    border: 'solid 5px',
    borderColor: '#B42026'
  },

  button: {
    color: 'white',

    backgroundColor: '#B42026'
  }

}));